import React from 'react';
let MoneyButton = require('@moneybutton/react-money-button').default

 var convertToHex=(str)=> {
  var hex = '';
  for (var i = 0; i < str.length; i++) {
      hex += '' + str.charCodeAt(i).toString(16);
  }
  return hex;
}


export default class ReplyForm extends React.Component {
  constructor(props){
    super(props);
    this.state={
      reply:''
    }

  }

  inputChangeHandler=(e)=>{
    this.setState({
      reply:e.target.value
    })
    
  }

  render() {
    return (
      <div className="new-reply-container">
        <div className=" w-form">
          <form id="email-form-2" name="email-form-2" data-name="Email Form 2" className="m-vote-price-form">
            <div className="div-block">
              <div style={{display:'flex',alignItems:'center'}}>Replying to: {this.props.replyingTo.username}</div>
              <div className="login-cancel" onClick={this.props.hideReply}>X</div>
            </div>
            <textarea onChange={this.inputChangeHandler} id="field" name="field" placeholder="Example Text" maxLength={5000} className="textarea w-input" defaultValue={""} />
            <MoneyButton
              outputs={[
                {
                  amount: 0,
                  currency: 'BSV',
                  type: 'SCRIPT',
                  script: 'OP_RETURN ' + convertToHex('13beK8bifnhuC8pawWvMrPCaWoTQi5LnZf') + ' ' + convertToHex('comment') + ' ' + convertToHex(this.props.replyingTo.path) + ' ' + convertToHex(encodeURIComponent(this.state.reply)) + ' ' + convertToHex(this.props.username) +' ' + convertToHex(this.props.userId)

                }, {
                  to: this.props.replyingTo.address,
                  amount: '0.01',
                  currency: 'USD'

                }
              ]}
              onPayment={this.props.hideReply}

            />
          </form>
        </div>
      </div>
    )
  }
}