import React from 'react';

export default function Comment(props) {

  var tx={...props.commentTree.tx};

  var path = tx.out[0].s3+tx.tx.h;
  var username = tx.out[0].s5;
  var address = tx.in[0].e.a
  var showReplyButton = path.length/64<6 && props.userId!=tx.out[0].s6

  var subcomments = {...props.commentTree};
  delete subcomments.tx;
  delete subcomments.comments;

  subcomments = Object.values(subcomments);

  var commentElements=[];

  subcomments.sort(function(a,b){
    return b.comments-a.comments;
  })

  for(let i=0;i<subcomments.length;i++){
    commentElements.push(<Comment replyHandler={props.replyHandler} userId={props.userId} commentTree={subcomments[i]}/>)
  }
  

  
  return (
    <div className="comment">
      <div className="comment-metadata">

        <div className="metadata">{tx.out[0].s5}</div>
        <div className="metadata">{tx.timeago}</div>
        {showReplyButton?<div className="metadata metadata-button" onClick={() => props.replyHandler(path, username, address )}> reply</div>:null}
      </div>
      <div className="comment-content">{decodeURIComponent(tx.out[0].s4)}</div>
      {commentElements}
    </div>
  )
}