import React from 'react';
import Cookies from 'universal-cookie';
let MoneyButton = require('@moneybutton/react-money-button').default

Number.prototype.countDecimals = function () {
  if(Math.floor(this.valueOf()) === this.valueOf()) return 0;
  return this.toString().split(".")[1].length || 0; 
}

export default class LoginAlert extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      input: '',
      priceInput: '',
      validPrice: false
    }
  }

  onPaymentHandler = (payment) => {

    if (this.state.input != '') {
      localStorage.setItem('userId', payment.userId.toString(), { path: '/' });
      localStorage.setItem('username', this.state.input, { path: '/' });
      //checks if change price alert
      if(!this.props.changingUsername&&!this.props.changingUsername) {
        localStorage.setItem('votePrice',this.state.priceInput);}
      this.props.loginHandler();
    }

    console.log(payment.userId)
    console.log(payment);
  }

  inputOnChangeHandler = (event) => {
    this.setState({
      input: event.target.value
    })
  }

  priceInputChangeHandler = event =>{
    var priceInputValue = event.target.value;
    var NaN = isNaN(priceInputValue);
    var validPrice=false;
    var price;

    if(!NaN){
      if(priceInputValue=="0."||priceInputValue=="0.0"){
        price=0;
      } else{
        var price = JSON.parse(event.target.value)
      }
      
      if(price.countDecimals()<3 & price>=0.01){
        validPrice=true;
      }
    } 
    this.setState({
      priceInput: event.target.value,
      validPrice
      
    })
  }

  votePriceChangeHandler=()=>{
    localStorage.setItem('votePrice',this.state.priceInput);
    this.props.votePriceChangeHandler();
  }


  render() {
    console.log(this.props.changingUsername);
    var paymentAmount;
    var login = !this.props.changingUsername & !this.props.changingVotePrice;
    var username = this.props.changingUsername;
    var vote = this.props.changingVotePrice;
    var validPriceChange = vote && this.state.validPrice && this.state.priceInput!='';

    var disableMoneyButton;
    if(login){
      disableMoneyButton=!(this.state.validPrice && this.state.input!=='')
    } else {
      disableMoneyButton=!(username&&this.state.input!=='')
    }
    
   

    username ? paymentAmount = '1' : paymentAmount = '0.01';
    var alertHeight;
    if (login) {
      alertHeight = 230
    } else if (username) {
      alertHeight = 140
    } else {
      alertHeight = 150
    }
    console.log(alertHeight)

    var header;
    if (login) {
      header = 'Login';
    } else if (username) {
      header = "Change Username";
    } else {
      header = "Change Vote Price";
    }

    var loginInfoClass = 'login-info';
    var loginMessage = "*On bsvnews, you can upvote other users' content. Set the default amount of money you would like to give them for each upvote. You can change this later. Your default price must be $0.01 or higher."
    if ((login || vote) && (this.state.validPrice == false & this.state.priceInput!='')) {
      loginInfoClass += ' login-info-red';
      loginMessage = "*Please enter a valid vote price (ex. 0.25)."
    }
    var loginInfo = (
      <div class={loginInfoClass}>{loginMessage}</div>

    )

    var usernameInput = (
      <input type="text" class="login-username w-input" maxLength="15" name="name-2"
        data-name="Name 2" placeholder="Desired username" id="name-2"
        onChange={this.inputOnChangeHandler} />
    )

    var voteInput;
    if (login) {
      voteInput = (
        <div className="vote-price-container">
          <input type="text" onChange={this.priceInputChangeHandler}
          class="login-username w-input" maxlength="5" name="name-2" data-name="Name 2"
          placeholder="Default vote price (USD)" id="name-2" />
        </div>
        
      )
    } else if (vote) {
      var buttonClass;
      this.state.validPrice?buttonClass='header-button':buttonClass='header-button button-greyed'
      voteInput = (
      <div className='vote-price-container'>
        <input type="text" onChange={this.priceInputChangeHandler}
          class="login-username w-input vote-price" maxlength="5" name="name-2" data-name="Name 2"
          placeholder="Default vote price (USD)" id="name-2" />
        <div className={buttonClass} onClick={validPriceChange?this.votePriceChangeHandler:()=>null}>
          <div className='header-button-text dropdown-text'>set price</div>
        </div>

      </div>
      );

    }

    return (
      <div class="login-container">
        <div class="login-alert w-form" style={{ height: alertHeight }}>
          <form id="email-form-3" name="email-form-3" data-name="Email Form 3" class="login-form">

            <div class="x-container" >
              <div className="div-block-4">
                <div class="login-title">{header}</div>
              </div>

              <div class="login-cancel" onClick={this.props.xHandler}>X</div>
            </div>
            <form id="email-form-3" name="email-form-3" data-name="Email Form 3" class="login-form">
              <div class="inputs-container">
                {login || username ? usernameInput : null}
                {login || vote ? [voteInput, loginInfo] : null}


              </div>
            </form>

            {login  || this.props.changingUsername ? <div className="mb-container" ><MoneyButton
              to='112'
              label='submit'
              amount={paymentAmount}
              currency='USD'
              onPayment={this.onPaymentHandler}
              disabled={disableMoneyButton}
            /></div> : null}
          </form>

        </div>
      </div>

    );
  }
}

