import React from 'react';
import Comment from './Comment';


export default function CommentContainer(props){

    var commentsArray = props.comments.slice();

    commentsArray.sort(function (a,b){
      return b.comments - a.comments;
    })

    var commentElements = [];
    if(props.comments){
      for(let i=0;i<props.comments.length;i++){
        commentElements.push(<Comment replyHandler={props.replyHandler} userId={props.userId} commentTree={commentsArray[i]}/>)
      }
    }
    

    return(
        <div className="comments-container">
          {commentElements}
          {commentElements.length==0?"No comments!":null}
        </div>
    )
}