import React from 'react';
let MoneyButton = require('@moneybutton/react-money-button').default

function convertToHex(str) {
    var hex = '';
    for (var i = 0; i < str.length; i++) {
        hex += '' + str.charCodeAt(i).toString(16);
    }
    return hex;
}

Number.prototype.countDecimals = function () {
    if (Math.floor(this.valueOf()) === this.valueOf()) return 0;
    return this.toString().split(".")[1].length || 0;
}


export default class PostForm extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            url: '',
            title: '',
            opReturn: '',
            postPrice: '',
            validPrice: false

        }
    }




    urlChangeHandler = (e) => {
        this.setState({
            url: e.target.value
        })
    }

    titleChangeHandler = (e) => {
        this.setState({
            title: e.target.value
        })

    }

    postPriceChangeHandler = (e) => {
        var NaN = isNaN(e.target.value);
        var validPrice = false;
        if (!NaN) {
            var price = JSON.parse(e.target.value)
            if (price.countDecimals() < 3 & price >= 0.01) {
                validPrice = true;
            }
        }
        this.setState({
            postPrice: e.target.value,
            validPrice

        })

    }


    defaultPriceHandler = () => {
        document.getElementsByClassName('price-field')[0].value = localStorage.getItem('votePrice');
        this.setState({
            validPrice: true,
            postPrice: localStorage.getItem('votePrice')
        });
    }

    render() {
        var opreturn = convertToHex('13beK8bifnhuC8pawWvMrPCaWoTQi5LnZf') + ' ' + convertToHex('post/vote') + ' ' + convertToHex('post') + ' ' + convertToHex(encodeURIComponent(this.state.title)) + ' ' + convertToHex(encodeURI(this.state.url)) + ' ' + convertToHex(this.props.username) + ' ' + convertToHex(this.props.userId)
        var checkedPrice=null;
       
        if(this.state.validPrice){
            checkedPrice=this.state.postPrice
        }
        return (
            <div className="new-post w-form">
                <form id="email-form" name="email-form" data-name="Email Form" className="form">
                    <input onChange={this.titleChangeHandler} type="text" className="text-field w-input" maxLength={256} name="name" data-name="Name" placeholder="Post Title" id="name" />
                    <input onChange={this.urlChangeHandler} type="email" className="text-field w-input" maxLength={256} name="email" data-name="Email" placeholder="Post URL" id="email" required />
                    <div class="post-price-container">
                        <input type="text" id='postPriceInput' onChange={this.postPriceChangeHandler} class="text-field price-field w-input" maxlength="256"
                            name="name-2" data-name="Name 2" placeholder="Vote Price (USD)" id="name-2" />
                        <div class="header-button" onClick={this.defaultPriceHandler}>
                            <div class="header-button-text dropdown-text">default price</div>
                        </div>
                    </div>
                    <div className="text-block-7">*Enter in the price of your initial vote on your own post. You can think of it as paying for a form of advertisement as your post may get noticed more. Must be more than $0.01.</div>
                    <MoneyButton
                        outputs={[
                            {
                                amount: 0,
                                currency: 'BSV',
                                type: 'SCRIPT',
                                script: 'OP_RETURN ' + opreturn

                            }, {
                                to: '1JM394PQB47chcs49PCe3JETLDaBwzieBP',
                                amount: checkedPrice,
                                currency: 'USD'

                            }
                        ]}
                        
                        label='submit'
                        disabled={!this.state.validPrice}


                        onPayment={this.props.finishSubmitHandler}
                    />
                </form>

            </div>
        );
    }
}