import React from 'react';
import windowSize from 'react-window-size';

class SegmentedController extends React.Component {

    render() {
        var mobile = this.props.windowWidth < 480
        var iOS = navigator.userAgent.includes('iPhone');

        if (mobile & iOS) {
            return (
                <div class="m-n-segmented-controller-container">
                    <div class="m-n-segmented-controller">
                        <div class={"m-n-tab-button"}
                            onClick={() => { this.props.switchMode('top') }}>
                            <div class="m-n-tab-button-text">Top</div>
                        </div>
                        <div class={"m-n-tab-button"} onClick={() => this.props.switchMode('new')} >
                            <div class="m-n-tab-button-text">New</div>
                        </div>

                        <div class={"m-n-tab-button"} onClick={() => this.props.switchMode('weekly')} >
                            <div class="m-n-tab-button-text">Weekly</div>
                        </div>

                    </div>
                </div>
            )
        } else if (mobile & !iOS) {
            return (
            <div class="m-n-segmented-controller-container">
                <div class="m-n-segmented-controller">
                    <div class={this.props.top ? "m-n-tab-button m-n-tab-button-selected" : "m-n-tab-button"}
                        onClick={() => { this.props.switchMode('top') }}>
                        <div class="m-n-tab-button-text">Top</div>
                    </div>
                    <div class={this.props.newMode ? "m-n-tab-button m-n-tab-button-selected" : "m-n-tab-button"} onClick={() => this.props.switchMode('new')} >
                        <div class="m-n-tab-button-text">New</div>
                    </div>

                    <div class={this.props.weekly ? "m-n-tab-button m-n-tab-button-selected" : "m-n-tab-button"} onClick={() => this.props.switchMode('weekly')} >
                        <div class="m-n-tab-button-text">Weekly</div>
                    </div>

                </div>
            </div>)
        }
        else {
            return null;
        }

    }
}

export default windowSize(SegmentedController);