import React, { Component } from 'react';
import './App.css';
import Post from './components/Post.js';
import Cookies from 'universal-cookie';
import LoginAlert from './components/LoginAlert';
import ReplyForm from './components/ReplyForm';
import PostForm from './components/PostForm';
import SegmentedController from './components/SegmentedController';
import { Helmet } from 'react-helmet';
import MobileVoteAlert from './components/MobileVoteAlert';
let MoneyButton = require('@moneybutton/react-money-button').default

function chunkString(str, len) {
  var _size = Math.ceil(str.length / len),
    _ret = new Array(_size),
    _offset
    ;

  for (var _i = 0; _i < _size; _i++) {
    _offset = _i * len;
    _ret[_i] = str.substring(_offset, _offset + len);
  }

  return _ret;
}

function validURL(str) {
  var pattern = new RegExp('^(https?:\\/\\/)?' + // protocol
    '((([a-z\\d]([a-z\\d-]*[a-z\\d])*)\\.)+[a-z]{2,}|' + // domain name
    '((\\d{1,3}\\.){3}\\d{1,3}))' + // OR ip (v4) address
    '(\\:\\d+)?(\\/[-a-z\\d%_.~+]*)*' + // port and path
    '(\\?[;&a-z\\d%_.~+=-]*)?' + // query string
    '(\\#[-a-z\\d_]*)?$', 'i'); // fragment locator
  return !!pattern.test(str);
}


class App extends Component {


  constructor(props) {
    super(props);
    //this.cookies = new Cookies();
    const username = localStorage.getItem('username');
    const userId = localStorage.getItem('userId');
    const votePrice = localStorage.getItem('votePrice');

    var state = {
      showLoginAlert: false,
      showDropdown: false,
      changingUsername: false,
      changingVotePrice: false,
      defaultVotePrice: null,
      loggedIn: false,
      replying: false,
      replyingTo: null,
      posting: false,
      showCommentsFor: null,
      commentsTransactions: null,
      comments: null,
      mode: 'top',
      userId: null,
      username: null,
      transactions: [],
      topPosts:[],
      newPosts:[],
      weeklyPosts:[]
    }

    if (username && userId && votePrice) {
      state.username = username;
      state.userId = userId;
      state.votePrice = votePrice;
      state.loggedIn = true;
    }
    this.state = state;
    this.loadPosts();
    this.createPostVoteSocket();

  }





  processPostsVotes = (transactions) => {
    //make sure there are the right number of pushdata's

    var posts = {};
    var votes = [];

    //filter invalid tx's and separate posts from votes
    var transactions = transactions.filter(tx => {
      var valid = false
      if (tx.out[0].s3) {
        if (tx.out[0].s3 == 'post') {
          //checks for the right number of pushdata's
          valid = tx.out[0].s7 && (validURL(decodeURI(tx.out[0].s5)) || decodeURI(tx.out[0].s5).includes('medium'))
          if (valid) {
            //donation sometimes happens in second output
            var secondOutput = false
            if (tx.out[2]) {
              secondOutput = tx.out[2].e.a == '1JM394PQB47chcs49PCe3JETLDaBwzieBP'
            }
            //checks both outputs for donation
            var donated = tx.out[1].e.a == '1JM394PQB47chcs49PCe3JETLDaBwzieBP' | secondOutput
            //adds valid post to posts object
            if (donated) {
              posts[tx.tx.h] = { tx: tx, reward: 0, voters: [] }

            }

          }


        } else if (tx.out[0].s3 == 'vote') {
          valid = tx.out[0].s5;
          if (valid) {
            votes.push(tx);

          }

        }

      }
      return valid;
    })
    



    //loop through votes to assign them properly
    for (let i = 0; i < votes.length; i++) {
      //does vote have corresponding post?
      var postTxid = votes[i].out[0].s4
      if (posts[postTxid]) {
        var correctAddress = posts[postTxid].tx.in[0].e.a == votes[i].out[1].e.a;
        var correctAddressSecondOutput = false;
        if (votes[i].out[2]) {
          correctAddressSecondOutput = posts[postTxid].tx.in[0].e.a == votes[i].out[2].e.a;
        }

        correctAddress = correctAddress || correctAddressSecondOutput;
        var voterId = votes[i].out[0].s5;
        var prevVoters = posts[postTxid].voters;
        var selfVote = voterId == posts[postTxid].tx.out[0].s7;

        if (correctAddress && !prevVoters.includes(voterId) && !selfVote) {
          //tabulating upvote total
          correctAddressSecondOutput ? posts[postTxid].reward += votes[i].out[2].e.v : posts[postTxid].reward += votes[i].out[1].e.v;
          prevVoters.push(voterId) // keeps track of unique voters

        }
      }
    }



    var postsArray = Object.values(posts)

    

    var weeklyPosts = postsArray.slice().sort((a, b) => {
      if (a.reward == b.reward) {

        return a.tx.timestamp - b.tx.timestamp;
      } else {
        return b.voters.length - a.voters.length
      }

    });

    var topPosts =weeklyPosts.filter(post => {
      return post.tx.timestamp > Date.now() - 86400000;
    })


    while (topPosts.length > 10) {
      topPosts.pop();
    }

    while(weeklyPosts.length>15){
      weeklyPosts.pop();
    }

    var newPosts = postsArray.filter(post => {
      return post.tx.timestamp > Date.now() - 86400000;
    });

    
   


    this.setState({
      weeklyPosts,topPosts,newPosts
    })

  }



  loadPosts = () => {


    var query = { "v": 3, "q": { "find": { "out.s1": "13beK8bifnhuC8pawWvMrPCaWoTQi5LnZf", "out.s2": "post/vote" }, "limit": 1000 } };
    var b64 = btoa(JSON.stringify(query));
    var url;
    // if (this.state.dateRange == "today") {
    //   url = "https://chronos.bitdb.network/q/1P6o45vqLdo6X8HRCZk8XuDsniURmXqiXo/" + b64;
    // } else {
    url = "https://weekly.bitdb.network/q/12KQyEvGbHcJmavgmB1jDeTSJhJTzQ5ePg/" + b64;

    // }


    var header = {
      headers: { key: "1EsRL44h3xNm8G4hT35ns3nNPicAHWAftc" }
    };

    fetch(url, header).then(function (r) {
      return r.json()
    }).then((r) => {
      var result = JSON.stringify(r, null, 2);
      result = JSON.parse(result);

      var transactions = result.t;


      this.processPostsVotes(transactions);
      this.setState({
        transactions
      })


    })
  }

  createPostVoteSocket = () => {

    var socketQuery = { "v": 3, "q": { "find": { "out.s1": "13beK8bifnhuC8pawWvMrPCaWoTQi5LnZf", "out.s2": "post/vote" } } };

    var b64 = btoa(JSON.stringify(socketQuery))
    // Subscribe to EventSource
    var url = "https://chronos.bitdb.network/s/1P6o45vqLdo6X8HRCZk8XuDsniURmXqiXo/" + b64;
    var socket = new EventSource(url)
    // Handle new messages

    socket.onmessage = (e) => {

      var transactions = JSON.parse(JSON.stringify(JSON.parse(e.data), null, 2)).data
      // if (transactions.length > 0) {
      //   this.loadPosts()
      // }

      var updatedTransactions = this.state.transactions.concat(transactions)
      this.processPostsVotes(updatedTransactions)
      this.setState({
        transactions: updatedTransactions
      });




    }

  }



  hideLoginAlert = () => {
    this.setState({
      showLoginAlert: false
    })
  }

  submitHandler = () => {
    if (!this.state.loggedIn) {
      this.setState({
        showLoginAlert: true
      })
    } else if (this.state.posting == true) {
      this.setState({
        posting: false
      })
    }
    else {
      this.setState({
        replying: false,
        posting: true,
        mode: 'new',
        
      }
      );

    }
  }


  switchMode = mode => {
    if (mode == 'top') {

      this.setState({ mode: 'top'})
    } else if (mode == 'new') {
      this.setState({ mode: 'new' })
    } else {
      this.setState({ mode: 'weekly'})
    }

  }

  loginHandler = () => {
    this.setState({
      loggedIn: true,
      userId: localStorage.getItem('userId'),
      username: localStorage.getItem('username'),
      votePrice: localStorage.getItem('votePrice')
    })
    this.hideLoginAlert();
  }

  replyHandler = (path, username, address) => {
    if (this.state.loggedIn) {
      this.setState({
        replying: true,
        replyingTo: {
          path,
          username,
          address
        }
      })
    } else {
      this.setState({ showLoginAlert: true });
    }

  }

  hideReplyFormHandler = () => {
    this.setState({
      replying: false,
      replyingTo: null
    })
  }

  toggleCommentsHandler = (postTransaction) => {
    if (this.state.showCommentsFor == postTransaction.tx.h) {
      // this.commentsSocket = null;
      this.setState({
        showCommentsFor: null
      })
    } else {
      //this.commentsSocket = null;
      this.loadComments(postTransaction);



    }


  }

  processComments = (transactions, postTransaction) => {
    var transactions = transactions.filter(tx => {
      var valid = false
      if (tx.out[0].s6) {
        valid = true
      }


      return valid;
    })


    var comments = {};
    var maxDepth = 0;
    var rootPath = postTransaction.tx.h;
    var sendAddress = postTransaction.in[0].e.a


    //loops to find comments of depth=1 and adds to comments object
    for (let i = 0; i < transactions.length; i++) {
      var correctSendAddress = (sendAddress == transactions[i].out[1].e.a) || (sendAddress == transactions[i].out[2].e.a);
      var selfComment = transactions[i].out[0].s6 == postTransaction.out[0].s7;

      if (transactions[i].out[0].s3.length / 64 > maxDepth) {
        maxDepth = transactions[i].out[0].s3.length / 64;
      }
      if (transactions[i].out[0].s3 == postTransaction.tx.h && correctSendAddress && !selfComment) {

        comments[transactions[i].tx.h] = {
          tx: transactions[i],
          comments: 0

        }
      }
    }


    if (maxDepth > 1) {
      for (let i = 0; i < transactions.length; i++) {


        if (transactions[i].out[0].s3.length == 64 * 2) {
          var pathArray = chunkString(transactions[i].out[0].s3, 64);
          if (comments[pathArray[1]]) {
            var sendAddress = comments[pathArray[1]].tx.in[0].e.a
            var correctSendAddress = (sendAddress == transactions[i].out[1].e.a) || (sendAddress == transactions[i].out[2].e.a)
            var selfComment = comments[pathArray[1]].tx.out[0].s6 == transactions[i].out[0].s6;
            if (correctSendAddress && !selfComment) {
              comments[pathArray[1]][transactions[i].tx.h] = {
                tx: transactions[i],
                comments: 0
              }
              comments[pathArray[1]].comments += 1

            }

          }


        }
      }

    }

    if (maxDepth > 2) {
      for (let i = 0; i < transactions.length; i++) {
        if (transactions[i].out[0].s3.length == 64 * 3) {
          var pathArray = chunkString(transactions[i].out[0].s3, 64);
          if (comments[pathArray[1]]) {
            if (comments[pathArray[1]][pathArray[2]]) {
              var sendAddress = comments[pathArray[1]][pathArray[2]].tx.in[0].e.a
              var correctSendAddress = (sendAddress == transactions[i].out[1].e.a) || (sendAddress == transactions[i].out[2].e.a)
              var selfComment = comments[pathArray[1]][pathArray[2]].tx.out[0].s6 == transactions[i].out[0].s6;
              if (correctSendAddress && !selfComment) {
                comments[pathArray[1]][pathArray[2]][transactions[i].tx.h] = {
                  tx: transactions[i],
                  comments: 0
                }
                comments[pathArray[1]][pathArray[2]].comments += 1
              }

            }
          }
        }
      }

    }

    if (maxDepth > 3) {
      for (let i = 0; i < transactions.length; i++) {
        if (transactions[i].out[0].s3.length == 64 * 4) {
          var pathArray = chunkString(transactions[i].out[0].s3, 64);
          if (comments[pathArray[1]]) {
            if (comments[pathArray[1]][pathArray[2]]) {
              if (comments[pathArray[1]][pathArray[2]][pathArray[3]]) {
                var sendAddress = comments[pathArray[1]][pathArray[2]][pathArray[3]].tx.in[0].e.a
                var correctSendAddress = (sendAddress == transactions[i].out[1].e.a) || (sendAddress == transactions[i].out[2].e.a)

                var selfComment = comments[pathArray[1]][pathArray[2]][pathArray[3]].tx.out[0].s6 == transactions[i].out[0].s6;
                if (correctSendAddress && !selfComment) {
                  comments[pathArray[1]][pathArray[2]][pathArray[3]][transactions[i].tx.h] = {
                    tx: transactions[i],
                    comments: 0
                  }
                  comments[pathArray[1]][pathArray[2]][pathArray[3]].comments += 1
                }
              }
            }
          }
        }
      }

    }
    if (maxDepth > 4) {
      for (let i = 0; i < transactions.length; i++) {
        if (transactions[i].out[0].s3.length == 64 * 5) {
          var pathArray = chunkString(transactions[i].out[0].s3, 64);
          if (comments[pathArray[1]]) {
            if (comments[pathArray[1]][pathArray[2]]) {
              if (comments[pathArray[1]][pathArray[2]][pathArray[3]]) {
                if (comments[pathArray[1]][pathArray[2]][pathArray[3]][pathArray[4]]) {
                  var sendAddress = comments[pathArray[1]][pathArray[2]][pathArray[3]][pathArray[4]].tx.in[0].e.a
                  var correctSendAddress = (sendAddress == transactions[i].out[1].e.a) || (sendAddress == transactions[i].out[2].e.a)

                  var selfComment = comments[pathArray[1]][pathArray[2]][pathArray[3]][pathArray[4]].tx.out[0].s6 == transactions[i].out[0].s6;
                  if (correctSendAddress && !selfComment) {
                    comments[pathArray[1]][pathArray[2]][pathArray[3]][pathArray[4]][transactions[i].tx.h] = {
                      tx: transactions[i],
                      comments: 0
                    }
                    comments[pathArray[1]][pathArray[2]][pathArray[3]][pathArray[4]].comments += 1
                  }
                }
              }
            }
          }
        }
      }

    }


    this.setState({
      comments: Object.values(comments),
      showCommentsFor: rootPath

    })


  }

  loadComments = (postTransaction) => {
    var rootPath = postTransaction.tx.h
    var query = { "v": 3, "q": { "find": { "out.s1": "13beK8bifnhuC8pawWvMrPCaWoTQi5LnZf", "out.s2": "comment", "out.s3": { "$regex": ".*" + rootPath + ".*" } } } };
    var b64 = btoa(JSON.stringify(query));
    var url = "https://weekly.bitdb.network/q/12KQyEvGbHcJmavgmB1jDeTSJhJTzQ5ePg/" + b64;


    var header = {
      headers: { key: "1EsRL44h3xNm8G4hT35ns3nNPicAHWAftc" }
    };

    fetch(url, header).then(function (r) {
      return r.json()
    }).then((r) => {
      var result = JSON.stringify(r, null, 2);
      result = JSON.parse(result);

      var transactions = result.t;
      this.processComments(transactions, postTransaction);

      this.setState({
        commentsTransactions: transactions,
      }
        // , () => this.createCommentsSocket(postTransaction)
      )



    })
  }

  createCommentsSocket = (postTransaction) => {

    var rootPath = postTransaction.tx.h
    var socketQuery = { "v": 3, "q": { "find": { "out.s1": "13beK8bifnhuC8pawWvMrPCaWoTQi5LnZf", "out.s2": "comment", "out.s3": { "$regex": ".*" + rootPath + ".*" } } } };

    var b64 = btoa(JSON.stringify(socketQuery))
    // Subscribe to EventSource
    var url = "https://chronos.bitdb.network/s/1P6o45vqLdo6X8HRCZk8XuDsniURmXqiXo/" + b64;
    this.commentsSocket = new EventSource(url)
    // Handle new messages

    this.commentsSocket.onmessage = (e) => {

      var transactions = JSON.parse(JSON.stringify(JSON.parse(e.data), null, 2)).data

      var updatedTransactions = this.state.commentsTransactions.concat(transactions)
      if (this.state.showCommentsFor == postTransaction.tx.h) {
        this.processComments(updatedTransactions, postTransaction)
        this.setState({
          commentsTransactions: updatedTransactions
        });
      }




    }

  }



  
  votePriceChangeHandler = () => {
    this.state.votePrice = localStorage.getItem('votePrice');
    this.setState({
      showLoginAlert: false,
      changingVotePrice: false
    })
  }



  render() {
    var postElements = [];
    //var posts = this.state.posts.slice()
    var posts=[];
    var top = this.state.mode=='top';
    var newMode = this.state.mode=='new';
    var weekly = this.state.mode=='weekly';
    var today = !weekly;

  

    if(top){
      posts=this.state.topPosts
    } else if (newMode){
      posts=this.state.newPosts
    } else{
      posts=this.state.weeklyPosts
    }


   



    for (let i = 0; i < posts.length; i++) {
      postElements.push(<Post key={posts[i].tx.tx.h} defaultVotePrice={this.state.votePrice} loggedIn={this.state.loggedIn} postData={posts[i]} comments={this.state.comments} showCommentsFor={this.state.showCommentsFor} toggleComments={this.toggleCommentsHandler} replyHandler={this.replyHandler} userId={this.state.userId} />)

    }

    var dropdown = (
      <div className="dropdown-container">
        <div class="dropdown">
          <div class="header-button" onClick={() => this.setState({ showLoginAlert: true, changingUsername: true, changingVotePrice: false, showDropdown: false })}>
            <div class="header-button-text dropdown-text">change username</div>
          </div>
          <div class="header-button dropdown-button">
            <div class="header-button-text dropdown-text" onClick={() => this.setState({ showLoginAlert: true, changingUsername: false, showDropdown: false, changingVotePrice: true })}>change vote price<br /></div>
          </div>
        </div>
      </div>


    )

    var headerRight;
    if (this.state.loggedIn) {
      headerRight = (
        <div className="header-right" >
          <div class="header-button header-button-right m-header-button" onClick={this.submitHandler}><img
            src="https://uploads-ssl.webflow.com/5cb9fe80c114993f85aebed2/5cfd835e1c26ab620b063cea_bsvnews%20plus%20icon.png"
            width="14" height="14" alt="" class="image-2" /></div>
          <div class="header-button header-button-right m-header-button" onClick={this.loadPosts}><img
            src="https://uploads-ssl.webflow.com/5cb9fe80c114993f85aebed2/5cfd8266982a747870f23408_bsvnews%20refresh.png"
            width="14" height="14" alt="" class="image" /></div>
          <div className="header-button header-button-right" onClick={() => this.setState({ showDropdown: !this.state.showDropdown })}>
            <div className="header-button-text" >{this.state.username}</div>
            <div className="header-button-text button-symbol">{this.state.showDropdown ? '‚ñº' : '‚ñ∂'}</div>
          </div>

        </div>

      );
    } else {
      headerRight = (
        <div className="header-right">
          <div className="header-button header-button-right" onClick={() => this.setState({ showLoginAlert: true })}>
            <div className="header-button-text" >login</div>
          </div>
        </div>
      )
    }




    


    return (
      <div>
        {/* This site was created in Webflow. http://www.webflow.com */}
        {/* Last Published: Sat Apr 20 2019 23:09:33 GMT+0000 (UTC) */}
        <meta charSet="utf-8" />
        <title>bsvnews</title>
        <meta content="width=device-width, initial-scale=1, maximum-scale=1,user-scalable=0" name="viewport" />
        <meta content="Webflow" name="generator" />
        {/*[if lt IE 9]><![endif]*/}
        <Helmet>
          <title>bsvnews</title>
          <script src="https://tonicpow.com/tonic.js"></script>
        </Helmet>


        <div className="container w-container" style={{ paddingBottom: 10 }}>
          <div className="header">
            <div className="header-left" >
              <div className="title">BSV News</div>
              <div className='button-container'>
                {today? <div className="header-button" onClick={top?()=>this.switchMode('new'):()=>this.switchMode('top')}> <div className="header-button-text"  >{this.state.mode == 'top' ? 'new' : 'top'} </div> </div> : null}
                <div className="header-button" onClick={this.submitHandler}> <div className="header-button-text" >{this.state.posting ? " X " : "submit"}</div></div>
                <div className="header-button" onClick={this.loadPosts}> <div className="header-button-text" >reload</div></div>
                <div className="header-button" onClick={weekly?()=>this.switchMode('top'):()=>this.switchMode('weekly')}> <div className="header-button-text" >{this.state.mode == 'weekly' ? 'today' : 'weekly'}</div></div>

              </div>
            </div>

            {headerRight}


          </div>
          {this.state.showDropdown ? dropdown : null}

          {this.state.showLoginAlert ? <LoginAlert votePriceChangeHandler={this.votePriceChangeHandler} xHandler={this.hideLoginAlert} loginHandler={this.loginHandler} changingUsername={this.state.changingUsername} changingVotePrice={this.state.changingVotePrice} /> : null}
          {this.state.posting ? <PostForm username={this.state.username} userId={this.state.userId} finishSubmitHandler={this.submitHandler} /> : null}

          <div className="posts">
            {postElements}
          </div>

          <div className='metadata' style={{ width: '100%', padding: 10, paddingBottom: 0, paddingRight: 20, display: 'flex', justifyContent: 'flex-end' }}> disable scale  Created by $canyouhandleme</div>

        </div>
        <div className="tonic-pow">
          <div style={{ margin: 'auto' }} class="tonic" data-address="1BvrRxhXAVsZKyMnBT1STkXXVnsXrm7BNm"></div>
        </div>
        {this.state.replying ? <ReplyForm hideReply={this.hideReplyFormHandler} replyingTo={this.state.replyingTo} username={this.state.username} userId={this.state.userId} /> : null}
        <MobileVoteAlert votePrice={this.state.votePrice} />
        <SegmentedController switchMode={this.switchMode} top={top} newMode={newMode} weekly={weekly}/>

      </div>



    );
  }
}

export default App;
